import React from "react"
import { Typography, Grid, TextField, Input, Card, InputAdornment, Button, LinearProgress, Snackbar } from "@mui/material"
import MuiAlert from '@mui/material/Alert';
import axios from 'axios'

const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />
  })
function Hmac() {
    const [textMessage, setTextMessage] = React.useState('')
    const [blakeKey, setBlakeKey] = React.useState('')
    const [digestBytes, setDigestBytes] = React.useState(64)
    const [hashTag, setHashTag] = React.useState('')
    const [isSubmitting, setIsSubmitting] = React.useState(false)
    const [open, setOpen] = React.useState(false);
    const [errorMessage, setErrorMessage] = React.useState('')
    const handleClose = () => {
        setOpen(false)
    }

    const handleSubmit = () =>{
        
        setErrorMessage('')
        setOpen(false)
        setHashTag('')
        setIsSubmitting(true)
        const payload = {
            text_message: textMessage,
            blake_key: blakeKey,
            digest_bytes: digestBytes
        }
        axios.post('/hmac',payload).then(res=>{    
        setHashTag(res.data.hash_tag)
        setIsSubmitting(false)
      }).catch(err=>{
        setErrorMessage('Invalid Input Format')
        setOpen(true)
        setIsSubmitting(false)
      })

        
    }
    return (
        <div>
            {
                isSubmitting && <LinearProgress />
            } 
            <center>
            <Typography variant='h4'>
                HMAC Demo
            </Typography>
            </center>

            <Grid container>
                <Grid item sm ={4} xs={12}>
                    <TextField style={{margin:'18px'}} value={textMessage} multiline rows={6} fullWidth  onChange={(e)=>setTextMessage(e.target.value)} placeholder="Enter the Plain Text" variant="outlined" />
                    <TextField style={{margin:'18px'}} value={blakeKey} multiline rows={6} fullWidth  onChange={(e)=>setBlakeKey(e.target.value)} placeholder="Enter the Blake Key" variant="outlined" />
                    <Card variant="outlined" style={{height:"184px",display:"flex", margin:"18px", justifyContent:"center", alignItems:"center", width:'100%'}}>
                    <Card variant="outlined" style={{padding:"12px"}}>
                    <Typography variant='body1' style={{color:'#6c757d'}}>Enter Digest Bytes</Typography>
                    <Input
                        value={digestBytes}
                        onChange={(e)=>{
                          if(e.target.value === '')
                            setDigestBytes('')

                          if(e.target.value >= 1 && e.target.value<=64)
                            setDigestBytes(e.target.value)
                        }}
                        endAdornment={<InputAdornment position="end">Bytes&nbsp;</InputAdornment>}
                        type="number"
                      />
                     
                      </Card>
                      </Card>
                </Grid>
                <Grid item sm ={4} xs={12}>
                    <div style={{height:'100%',display:'flex', justifyContent:'center', alignItems:'center'}}>
                        <Button disabled={!!isSubmitting} onClick={handleSubmit} variant='outlined'>Submit</Button>
                    </div>
                </Grid>
                <Grid item sm ={4} xs={12}>
                <div style={{height:'100%',display:'flex', justifyContent:'center', alignItems:'center'}}>
                <TextField style={{margin:'18px'}} value={hashTag} multiline rows={16} fullWidth placeholder="Hash Tag" variant="outlined" />
                </div>
                </Grid>
            </Grid>
            <Snackbar
      anchorOrigin={{vertical:'bottom',horizontal:'center'}}
      autoHideDuration={3000}
      open={open}
      onClose={handleClose}
      >
      <Alert onClose={handleClose} severity="error" sx={{ width: '100%' }}>
      {errorMessage}
    </Alert></Snackbar>
        </div>
    )
}

export default Hmac
